<template>
  <!-- <div class="mainClass">
    <div class="headArea">Login</div>
    <div class="loginInfo">
      <div>
        <input type="text" v-model="username" placeholder="username" />
      </div>
      <div>
        <input type="password" v-model="password" placeholder="password" />
      </div>
    </div>
    <div class="btnArea">
      <div class="loginBtn" @click="subLogin">登錄</div>
    </div>
  </div> -->
  <div class="login-container">
    <h2 class="login-title"></h2>
    <a-form-model ref="form" :model="form" class="login-form" style="margin:0 auto">
      <h2 class="title">用户登录 LOGIN</h2>
      <a-form-model-item :wrapper-col="{ span: 14, offset: 5 }"> 
        <a-input  v-model="form.username">
          <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
        </a-input>
      </a-form-model-item>
      <a-form-model-item :wrapper-col="{ span: 14, offset: 5 }">
        <a-input-password v-model="form.password">
          <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
        </a-input-password>
      </a-form-model-item>
      <a-form-model-item :wrapper-col="{ span: 14, offset: 5 }">
        <a-radio-group v-model="form.model" style="color:#fff">
          <a-radio value="Mongoose" style="color:#fff">
            Mongoose
          </a-radio>
          <a-radio value="Badger" style="color:#fff">
            Badger
          </a-radio>
          <a-radio value="Bison" style="color:#fff">
            Bison
          </a-radio>
        </a-radio-group>
      </a-form-model-item>

      <a-form-model-item :wrapper-col="{ span: 14, offset: 5 }">
        <a-button class="submit" type="primary" @click="subLogin">登录</a-button>
      </a-form-model-item>
    </a-form-model>
  </div>
</template>
<script>
import router from '@/router'
import store from '@/store'
import { login, logout } from '@/api/login'
import security from '@/utils/security'
export default {
  data: function () {
    return {
      user:'../assets/username.png',
      mima:'../assets/password.png',
       form:{
        username: 'w20002678',
        password: '123',
        model:'Mongoose'
       }
    }
  },
  methods: {
    subLogin: function () {
      login(this.form).then(res => {
        if(res.code==200){
          security.setUser(res.data.role.replace(/\[|]/g,''));
          localStorage.setItem("token",res.data.token);
          localStorage.setItem("model",this.form.model);
          this.$message.success("登陸成功");
          router.push({ path: '/home/fix' })
        }
      })
    },

    goToRegistry: function () {
      router.push({ name: 'About' })
    }
  }
}
</script>
<style >
.login-form {
  width: 565px;
  height: 372px;
  margin: 0 auto;
  background: url("../assets/user.png");
}

/* 背景 */
.login-container {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url("../assets/bg.png");
  background-repeat: no-repeat;
}

/* Log */
.login-title {
  color: #fff;
  text-align: center;
  margin: 150px 0;
  font-size: 48px;
  font-family: Microsoft Yahei;
}
/* 登陆按钮 */
.submit{
  width: 100%;
  height: 45px;
  font-size: 16px;
}
/* 用户登陆标题 */
.title{
  padding-top: 40px;
  margin-bottom: 30px;
  color: #fff;
  font-weight: 700;
  font-size: 24px;
  font-family: Microsoft Yahei;
}

.inputBox{
  height: 45px;
}
/* 输入框内左边距50px */
.ant-input-affix-wrapper .ant-input:not(:first-child) {
    padding-left: 50px;
}
</style>

