import CryptoJS from 'crypto-js'
import moment from 'moment'

const LOGINSECRETKEY = "default value"
const SALT = "consogog('+number+')"
const OFFSET = "window."
const LOGINTIME = String.fromCharCode(3)
const USERINFO = String.fromCharCode(4)
const map = {
    "/home":"home",
    "/home/user":"user",
    "/home/hc":"hc",
    "/home/hcy":"hcy",
    "/home/fix":"fix",
    "/home/fixy":"fixy",
}
export default  {
    admin:{        
        user :true,
        hc :true,
        hcy :true,
        fix:true,
        fixy:true,
        home:true
    },
    IE_1:{
        user :true,
        hc :true,
        hcy :true,
    },
    IE_2:{
        user :true,
        hc :true,
    },
    _1:{
        fix:true,
        fixy:true,
        home:true
    },
    _2:{
        fix:true,
        home:true
    },
    map:map,
    getByUrl:function(url){
        const role = this.getUser()
        let role_index 
        if(this[role] != null){
            role_index = role[role.length - 1]
        }
        if(this[role] != null || this["_"+role_index]){
            return Object.assign({},this[role],this["_"+role_index])[map[url]]
        }
        return false
    },
    getByKey:function(key){
        const role = this.getUser()
        let role_index = role[role.length - 1]
        if(this[role] != null || this["_"+role_index]){
            return Object.assign({},this[role],this["_"+role_index])[key]
        }
        return false
    },
    setUser: function (userObj) {
        this.clearUser()
        const loginTime = moment().format("YYYY-MM-DD HH:mm:ss") + LOGINSECRETKEY
        this.setLocalStorage(LOGINTIME, this.aesEncrypt(loginTime, LOGINSECRETKEY))
        this.setLocalStorage(USERINFO, this.aesEncrypt(JSON.stringify(userObj), this.getLoginTime() + SALT))
    },
    getLoginTime: function () {
        if (this.getLocalStorage(LOGINTIME) != null && this.getLocalStorage(LOGINTIME) !== "") {
            return this.aesDecrypt(this.getLocalStorage(LOGINTIME), LOGINSECRETKEY)
        } else {
            return null
        }
    },
    getUser: function () {
        const loginTime = this.getLoginTime()
        const userInfo = this.getLocalStorage(USERINFO)
        if (loginTime != null && loginTime !== "" && userInfo != null) {
            const userinfo = JSON.parse(this.aesDecrypt(userInfo, loginTime + SALT))
            return userinfo
        } else {
            return null
        }
    },
    setLocalStorage (key, value) {
        localStorage.setItem(key, value)
    },
    getLocalStorage (key) {
        return localStorage.getItem(key);
    },
    deleteLocalStorage (key) {
        localStorage.clear(key)
    },
    clearUser: function () {
        this.deleteLocalStorage(USERINFO)
        this.deleteLocalStorage(LOGINTIME)
    },
    // 加密
    aesEncrypt: function (data, secretKey) {
        while (true) {
            if (secretKey.length % 8 === 0) {
                break
            }
            secretKey += LOGINTIME
        }
        const result = CryptoJS.AES.encrypt(
            CryptoJS.enc.Utf8.parse(data),
            CryptoJS.enc.Utf8.parse(secretKey),
            {
                iv: CryptoJS.enc.Utf8.parse(OFFSET),// 偏移量
                mode: CryptoJS.mode.CBC,
                padding: CryptoJS.pad.Pkcs7,
            }
        )
        return result
    },
    // 解密
    aesDecrypt: function (cipher, secretKey) {
        while (true) {
            if (secretKey.length % 8 === 0) {
                break
            }
            secretKey += LOGINTIME
        }
        const decrypted = CryptoJS.AES.decrypt(
            cipher,
            CryptoJS.enc.Utf8.parse(secretKey),
            {
                iv: CryptoJS.enc.Utf8.parse(OFFSET), // 偏移量
                mode: CryptoJS.mode.CBC,
                padding: CryptoJS.pad.Pkcs7,
            }
        )
        return CryptoJS.enc.Utf8.stringify(decrypted)
    },
    enc: function (value) {
        return CryptoJS.enc.Utf8.parse(value)
    }
}